.form-page {
	display: flex;
	width: 100vw;
	margin-left: 0;
	height: 100vh;

	.leftside-menu-block {
		background-color: var(--left-side-menu-block);
		position: sticky;
		top: 0;
		left: 0;
		/* 		z-index: 4;
 */
		background-color: rgb(0, 0, 0);
		padding: 2vh 1vw;
	}
}
