.rightside-main-section {
	margin-left: 150px;
	width: 100%;
	display: flex;

	.form-main-container {
		padding: 0 10px 0;
		width: 100vw;

		h2.form-title {
			text-align: center;
		}
		span.form-subtitle {
			text-align: center;
		}
		.group {
			width: 400px;
			margin: 0;
		}
	}
}
