*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

:root {
	--primary-color: rgb(251, 197, 49);
	--secondary-color: black;
	--primary-color-m: rgb(255, 255, 255);
	--header-background-color: rgb(255, 255, 255);
	--border-color-header: GRAY;
	--font-primary-color: black;
	--font-secondary-color: white;
	--left-side-menu-block: white;
	--after-hover-light-color: white;
	--after-hover-dark-color: black;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: "Open Sans Condensed";
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

a {
	text-decoration: none;
	color: black;
}
